.tick {
    margin-right: 20px;
    cursor: pointer;
}

.tick input {
    display: none;
}

.tick--icon {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: 20px;
    height: 20px;
    padding: 2px;
    border: 1px solid #c2c9d5;
    border-radius: 50%;
    color: transparent;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
}

.tick input:checked + .tick--icon {
    border: 1px solid #236d84;
    /* background: linear-gradient(344deg, #236d84 0%, #41a4c1 100%); */
    background: #236d84;
}

.tick input:checked + .tick--icon-true {
    -webkit-animation: correctCheck;
            animation: correctCheck;
    -webkit-animation-delay: .7s;
            animation-delay: .7s;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.tick input:checked + .tick--icon-false {
    -webkit-animation: incorrectCheck;
            animation: incorrectCheck;
    -webkit-animation-delay: .7s;
            animation-delay: .7s;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.radio:hover .tick--icon {
    border: 1px solid #236d84;
    -webkit-box-shadow: 0 3px 16px rgba(31, 46, 74, 0.33);
            box-shadow: 0 3px 16px rgba(31, 46, 74, 0.33);
}



